import React, { Component, Fragment } from 'react';
import {
  Dialog,
  Typography,
  IconButton,
  Divider,
  withMobileDialog,
  DialogActions,
  Button,
  DialogContent,
} from '@material-ui/core';
import Info from '@material-ui/icons/Info';

class AdditionalInfoDialog extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
    };
  }

  handleClick = () => {
    this.setState(state => ({
      open: !state.open,
    }));
  };

  handleClose = () => {
    this.setState({
      open: false,
    });
  };

  render() {
    return (
      <Fragment>
        <IconButton onClick={this.handleClick}>
          <Info fontSize="small" />
        </IconButton>
        <Dialog
          fullWidth
          scroll="paper"
          fullScreen={this.props.fullScreen}
          maxWidth={this.props.maxWidth}
          open={this.state.open}
          onClose={this.handleClose}
        >
          <DialogContent style={{ padding: 15 }}>
            <Typography variant="h6">{this.props.titleTH}</Typography>
            <Typography color="textSecondary" variant="subtitle2">
              {this.props.titleEN}
            </Typography>
            <Divider style={{ marginTop: 15, marginBottom: 15 }} />
            <Typography gutterBottom variant="subtitle1">
              คำอธิบาย
            </Typography>
            {this.props.children}
            <br />
            <Typography variant="subtitle1">รูปแบบการกรอก</Typography>
            <Typography color="textSecondary">{this.props.format}</Typography>

            <DialogActions>
              <Button
                variant="outlined"
                color="primary"
                onClick={this.handleClose}
              >
                ปิด
              </Button>
            </DialogActions>
          </DialogContent>
        </Dialog>
      </Fragment>
    );
  }
}

export default withMobileDialog()(AdditionalInfoDialog);
