import React, { Component, Fragment } from 'react';
import currencyCodes from './shared/jsonData/currencycodes';
import { TextValidator } from 'react-material-ui-form-validator';
import { ListItem, ListItemText } from '@material-ui/core';

class CurrencyCode extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currencyCodeData: [],
      searchInput: '',
      validInput: false,
      selected: false,
      validators: ['required'],
      errorMessages: ['จำเป็นต้องกรอก'],
    };
    this.handleBlur = this.handleBlur.bind(this);
  }

  componentDidMount() {
    this.interval = null;
    this.setState({
      currencyCodeData: currencyCodes,
    });
  }

  componentWillUnmount() {
    this.setState({
      currencyCodeData: [],
    });
    clearTimeout(this.interval);
  }

  handleBlur(event) {
    event.preventDefault();
    this.interval = setTimeout(() => {
      this.setState(state => ({
        searchInput: state.selected ? state.searchInput : '',
      }));
    }, 200);
  }

  handleSeachInput = event => {
    event.preventDefault();
    this.setState({
      searchInput: event.target.value,
      selected: false,
    });
  };

  handleSelect = event => {
    event.preventDefault();
    this.setState({
      selected: true,
      searchInput: event.target.innerText.trim().slice(0, 3),
      validators: ['required', 'maxStringLength: 3'],
      errorMessages: ['จำเป็นต้องกรอก', 'กรอกได้ 3 ตัวอักษร'],
    });
  };

  render() {
    const { searchInput, validators, errorMessages, selected } = this.state;
    const { handleSeachInput, handleSelect, handleBlur } = this;
    const lowerCasedInput = searchInput.toLowerCase();

    const filteredCurrencyCodes = this.state.currencyCodeData.filter(
      country =>
        country.currencyCode.toLowerCase().includes(lowerCasedInput) ||
        country.name.toLowerCase().includes(lowerCasedInput) ||
        country.currencyName.toLowerCase().includes(lowerCasedInput)
    );

    return (
      <Fragment>
        <TextValidator
          id="currencyCode"
          name="currencyCode"
          fullWidth
          required
          margin="normal"
          style={{ margin: 8 }}
          label="Currency Code"
          helperText="รหัสสกุลเงินตรา"
          placeholder="ค้นหาด้วยสกุลเงิน / ชื่อประเทศ"
          type="search"
          onBlur={handleBlur}
          value={searchInput}
          validators={validators}
          errorMessages={errorMessages}
          onChange={handleSeachInput}
        />
        {selected || searchInput === '' ? (
          ''
        ) : (
          <Fragment>
            {filteredCurrencyCodes.slice(0, 5).map((country, i) => (
              <ListItem
                button
                key={i}
                onClick={handleSelect}
                value={country.currencyCode}
              >
                <ListItemText
                  primary={
                    <Fragment>
                      {`${country.currencyCode} `}
                      {`:: ${country.currencyName} `}
                      {`:: ${country.name}`}
                    </Fragment>
                  }
                />
              </ListItem>
            ))}
          </Fragment>
        )}
      </Fragment>
    );
  }
}

export default CurrencyCode;
