import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { InputAdornment } from '@material-ui/core';
import AdditionalInfoDialog from '../../../shared/additionalInfoDialog';

const OutsideReleasePort = props => {
  const { handleChange, outsideReleasePort } = props;
  return (
    <TextValidator
      name="outsideReleasePort"
      id="outsideReleasePort"
      onChange={handleChange}
      value={outsideReleasePort}
      label="Outside Release Port"
      placeholder="0000"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={['matchRegexp:^[0-9]*$', 'maxStringLength:4']}
      errorMessages={['กรอกได้เฉพาะตัวเลข', 'ต้องกรอกไม่เกิน 4 หลัก']}
      InputProps={{
        endAdornment: (
          <InputAdornment position="end">
            <AdditionalInfoDialog
              titleTH="รหัสสถานที่ตรวจปล่อยนอกสถานที่"
              titleEN="Outside Release Port"
              format="n..4"
              maxWidth="sm"
            />
          </InputAdornment>
        ),
      }}
    />
  );
};

export default OutsideReleasePort;
