import React, { Component, Fragment } from 'react';
import countryCodes from '../../../shared/countrycodes.json';
import { TextValidator } from 'react-material-ui-form-validator';
import { ListItem, ListItemText } from '@material-ui/core';

class CountryCodeSelector extends Component {
  constructor(props) {
    super(props);
    this.state = {
      countryCodeData: [],
      searchInput: '',
      timeOut: null,
      validInput: false,
      selected: false,
      validators: ['required'],
      errorMessages: ['จำเป็นต้องกรอก'],
    };
    this.handleBlur = this.handleBlur.bind(this);
  }

  componentDidMount() {
    this.interval = null;
    this.setState({
      countryCodeData: countryCodes,
    });
  }

  componentWillUnmount() {
    this.setState({
      countryCodeData: [],
    });
    clearTimeout(this.interval);
  }

  handleBlur(event) {
    event.preventDefault();
    this.interval = setTimeout(() => {
      this.setState(state => ({
        searchInput: state.selected ? state.searchInput : '',
      }));
    }, 200);
  }

  handleSeachInput = event => {
    event.preventDefault();
    this.setState({
      searchInput: event.target.value,
      selected: false,
    });
  };

  handleSelect = event => {
    event.preventDefault();
    this.setState({
      selected: true,
      searchInput: event.target.innerText.trim().slice(0, 2),
      validators: ['required', 'maxStringLength: 2'],
      errorMessages: ['จำเป็นต้องกรอก', 'กรอกได้ 2 ตัวอักษร'],
    });
  };

  render() {
    const { handleSeachInput, handleSelect, handleBlur } = this;
    const { id, name, label, helperText } = this.props;
    const {
      searchInput,
      validators,
      errorMessages,
      selected,
      countryCodeData,
    } = this.state;

    const lowerCasedInput = searchInput.toLowerCase();

    const filteredCountryCodes = countryCodeData.filter(
      country =>
        country['alpha-2'].toLowerCase().includes(lowerCasedInput) ||
        country.name.toLowerCase().includes(lowerCasedInput)
    );

    return (
      <Fragment>
        <TextValidator
          id={id}
          name={name}
          fullWidth
          required
          margin="normal"
          style={{ margin: 8 }}
          label={label}
          helperText={helperText}
          placeholder="ค้นหาด้วยชื่อประเทศ / รหัสประเทศ"
          type="search"
          onBlur={handleBlur}
          value={searchInput}
          validators={validators}
          errorMessages={errorMessages}
          onChange={handleSeachInput}
        />
        {selected || searchInput === '' ? (
          ''
        ) : (
          <Fragment>
            {filteredCountryCodes.slice(0, 5).map(country => (
              <ListItem
                button
                key={country['country-code']}
                onClick={handleSelect}
                value={country['alpha-2']}
              >
                <ListItemText
                  primary={
                    <Fragment>
                      {`${country['alpha-2']}`}
                      {` : ${country.name}`}
                    </Fragment>
                  }
                />
              </ListItem>
            ))}
          </Fragment>
        )}
      </Fragment>
    );
  }
}

export default CountryCodeSelector;
