import React, { Fragment, Component } from 'react';
import { DatePicker } from 'material-ui-pickers';

class ArrivalDate extends Component {
  state = {
    selectedDate: new Date(),
    selectedDateString: '',
  };

  handleDateChange = date => {
    this.setState({
      selectedDate: date,
    });
  };

  render() {
    const { selectedDate } = this.state;

    return (
      <Fragment>
        <DatePicker
          id="arrivalDate"
          label="Arrival Date"
          format="yyyyMMdd"
          placeholder="2018/12/31"
          helperText="ปี/เดือน/วัน"
          name="arrivalDate"
          fullWidth
          style={{ margin: 8 }}
          value={selectedDate}
          onChange={this.handleDateChange}
          mask={value =>
            value
              ? [
                  /\d/,
                  /\d/,
                  '/',
                  /\d/,
                  /\d/,
                  '/',
                  /\d/,
                  /\d/,
                  /\d/,
                  /\d/,
                ]
              : []
          }
        />
      </Fragment>
    );
  }
}

export default ArrivalDate;
