import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const DischargePort = props => {
  const { handleChange, dischargePort } = props;
  return (
    <TextValidator
      name="dischargePort"
      id="dischargePort"
      required
      onChange={handleChange}
      value={dischargePort}
      label="Discharge Port"
      helperText="รหัสสถานที่นำเข้า"
      placeholder="0000"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={['required', 'matchRegexp:^[0-9]*$', 'maxStringLength:4']}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'กรอกได้เฉพาะตัวเลข',
        'ต้องกรอกไม่เกิน 4 หลัก',
      ]}
    />
  );
};

export default DischargePort;
