import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const ReleasePort = props => {
  const { handleChange, releasePort } = props;
  return (
    <TextValidator
      name="releasePort"
      id="releasePort"
      required
      onChange={handleChange}
      value={releasePort}
      label="Release Port"
      helperText="รหัสสถานที่ตรวจปล่อย"
      placeholder="0000"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={['required', 'matchRegexp:^[0-9]*$', 'maxStringLength:4']}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'กรอกได้เฉพาะตัวเลข',
        'ต้องกรอกไม่เกิน 4 หลัก',
      ]}
    />
  );
};

export default ReleasePort;
