import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CargoTypeCode = props => {
  const { handleChange, cargoTypeCode } = props;
  return (
    <TextValidator
      name="cargoTypeCode"
      id="cargoTypeCode"
      required
      onChange={handleChange}
      value={cargoTypeCode}
      label="Cargo Type Code"
      helperText="ประเภทของการบรรจุตามมาตรฐาน UNECE Recommendation 21"
      style={{ margin: 8 }}
      fullWidth
      margin="normal"
      validators={[
        'required',
        'matchRegexp:^[0-9]$',
        'minNumber:0',
        'maxNumber:9',
      ]}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'ต้องเป็นตัวเลข 0-9 เท่านั้น',
        'ต้องกรอกเป็นตัวเลข 1 หลัก',
        'ต้องกรอกเป็นตัวเลข 1 หลัก',
      ]}
    />
  );
};

export default CargoTypeCode;
